<template>
  <div class="home">
    <!-- Slogan -->
    <div class="page-slogan">
      <p>Come on, bro!</p>
      <p id="sloganContent" style="display: none;">Wish you good fortune in the days to come.</p>
      <p id="showContent"></p>
    </div>
    <router-view></router-view>
  </div>
</template>

<script>
export default {
  data () {
    return {
      wish: ''
    }
  },

  mounted () {
    this.handleWords()
  },
  methods: {
    // 控制slogan文字定时出现
    handleWords () {
      window.onload = setTimeout(function () {
        const slogan = document.getElementById('sloganContent')
        const sc = document.getElementById('showContent')
        let i = 0
        const timer = setInterval(function () {
          sc.innerHTML = slogan.innerHTML.substring(0, i++)
          if (sc.innerHTML === slogan.innerHTML) {
            clearInterval(timer)
          }
        }, 300)
      }, 6000)
    }
  }
}
</script>

