import Vue from 'vue'
import VueRouter from 'vue-router'

import Home from '../views/Home.vue'
import Person from '../views/Person.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    component: Home,
    children: [
      { path: '', component: Person }
    ]
  }
]

const router = new VueRouter({
  routes
})

export default router
