import Vue from 'vue'
import App from './App.vue'
import router from './router'
import ElementUI, { Carousel } from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'

Vue.use(ElementUI)
Vue.component(Carousel.name, Carousel)

Vue.config.productionTip = false

new Vue({
  router,
  render: h => h(App)
}).$mount('#app')
